html {
	max-height: 100vh;
	max-width: 100vw;
	margin: 0;
}

body {
	margin: 0;
	max-height: 100vh;
	max-width: 100vw;
}

.login-form-wrapper {
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: center;
	overflow: hidden;
}

.login-form {
	margin-top: 45px;
	margin-bottom: 20px;
	border: 1px solid black;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 10px;
	border-radius: 10px;
	height: 600px;
	width: 400px;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.form-title {
	font-family: "Permanent Marker", cursive;
	color: #343434;
}

.form-subtitle {
	font-family: "Roboto", sans-serif;
	text-align: center;
	color: #343434;
}

.input-wrapper {
	display: flex;
	flex-direction: column;
	margin-bottom: 15px;
	width: 80%;
}

.input {
	padding: 2px;
	margin: 2px;
	border-bottom: 2px solid black;
	border-top: none;
	border-right: none;
	border-left: none;
	font-weight: 600;
}

.input:focus {
	outline: none;
}

.login-button {
	color: white;
	background-color: #343434;
	padding: 7px 6px 7px 6px;
	border-radius: 4px;
	border: none;
}

.login-button:hover {
	background-color: rgb(44, 44, 44);
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	cursor: pointer;
}

.cta {
	color: #343434;
}

